"use client"

import { useState } from "react"
import { Card } from "@/components/ui/card"
import { Switch } from "@/components/ui/switch"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { Slider } from "@/components/ui/slider"

export default function Home() {
  const [enabled, setEnabled] = useState(false)
  const [selectedColor, setSelectedColor] = useState("red")
  const [hotkey, setHotkey] = useState("F")
  const [isListening, setIsListening] = useState(false)
  const [smooth, setSmooth] = useState(5)

  const handleHotkeyChange = () => {
    setIsListening(true)
    const handleKeyPress = (e: KeyboardEvent) => {
      e.preventDefault()
      setHotkey(e.key.toUpperCase())
      setIsListening(false)
      window.removeEventListener("keydown", handleKeyPress)
    }
    window.addEventListener("keydown", handleKeyPress)
  }

  return (
    <main className="min-h-screen bg-black/95 text-white p-4">
      <div className="max-w-md mx-auto">
        <div className="flex items-center justify-between mb-8">
          <h1 className="text-2xl font-bold text-white">007</h1>
          <span className="text-zinc-500 text-sm">AimColor</span>
        </div>

        <Card className="bg-zinc-900/90 border-zinc-800 p-6">
          <div className="space-y-6">
            <div className="flex items-center justify-between">
              <div className="space-y-1">
                <h2 className="text-lg font-semibold">Globals</h2>
                <p className="text-sm text-zinc-400">Enable/Disable AimColor</p>
              </div>
              <Switch 
                checked={enabled}
                onCheckedChange={setEnabled}
                className="data-[state=checked]:bg-fuchsia-500"
              />
            </div>

            <div className="space-y-4">
              <div>
                <label className="text-sm text-zinc-400 mb-2 block">Target Color</label>
                <Select value={selectedColor} onValueChange={setSelectedColor}>
                  <SelectTrigger className="w-full bg-zinc-800/50 border-zinc-700">
                    <SelectValue />
                  </SelectTrigger>
                  <SelectContent className="bg-zinc-800 border-zinc-700">
                    <SelectItem value="red">Red</SelectItem>
                    <SelectItem value="yellow">Yellow</SelectItem>
                    <SelectItem value="purple">Purple</SelectItem>
                  </SelectContent>
                </Select>
              </div>

              <div>
                <label className="text-sm text-zinc-400 mb-2 block">Toggle Hotkey</label>
                <button
                  onClick={handleHotkeyChange}
                  className="w-full px-4 py-2 bg-zinc-800/50 border border-zinc-700 rounded-md text-left hover:bg-zinc-800/70 transition-colors"
                >
                  {isListening ? "Press any key..." : hotkey}
                </button>
              </div>

              <div>
                <div className="flex justify-between items-center mb-2">
                  <label className="text-sm text-zinc-400">Smooth</label>
                  <span className="text-sm text-zinc-400">{smooth}</span>
                </div>
                <Slider
                  value={[smooth]}
                  onValueChange={([value]) => setSmooth(value)}
                  max={10}
                  step={1}
                  className="[&_[role=slider]]:bg-fuchsia-500"
                />
              </div>
            </div>
          </div>
        </Card>
      </div>
    </main>
  )
}
